var _this;
import wInput from '@/components/watch-login/watch-input.vue' //input
import wButton from '@/components/watch-login/watch-button.vue' //button
import {User} from '@/static/js/Utils.js'
var app = getApp();
export default {
	data() { 
		return { 
			//logo图片
			logoImage:'https://one-panda-1257831346.cos.ap-shanghai.myqcloud.com/logo/logo.png',
			username:'', //学号
			password:'', //密码
			isRotate: false, //是否加载旋转
			   
			canIUse: wx.canIUse('button.open-type.getUserInfo')
		}; 
	},
	components:{  
		wInput, 
		wButton
	},
	mounted() {
		_this = this; 
	},
	methods: {
	    startLogin(){
			_this = this
			//登录
			if(this.isRotate){
				//判断是否加载中，避免重复点击请求
				return false;
			}
			if (this.username.length == "") {
			     uni.showToast({
			        icon: 'none',
					position: 'bottom',
			        title: '学号不能为空'
			    });
			    return;
			}
	        if (this.password.length == "") {
	            uni.showToast({
	                icon: 'none',
					position: 'bottom',
	                title: '密码不能为空'
	            });
	            return;
	        }
			let code;
			let userInfo;
			_this.isRotate = true;   
			















































			

			  User.getCode()
			  .then(res => {
			  	code = res; 
			    return User.getUserInfo();
			  })
			  .then(res => {
			  	userInfo = res;
			  	return User.userLogin(code,userInfo,_this.username,_this.password,0);
			  }) 
			  .then(res => {    //登录成功返回true 失败返回false
				 _this.isRotate = false;
			  	 if(res){
					uni.reLaunch({
						url:'/pages/course/course'
					})	
			  	 }else{
			  		uni.showToast({
			  		    icon: 'none',
			  			position: 'bottom',
			  		    title: '账号或者密码错误'
			  		});
			  	 }
			  })
			  .catch(error => {
				  console.log(error)
				 _this.isRotate = false;
			  	 uni.showToast({
			  		icon:"loading",
			  		title:"后台网络错误",
			  		duration:1000
			  	})
			  })

	    }
	}
}
